import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { FoursquareService } from '../../services/foursquare.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  form: FormGroup;

  results: {}[];

  constructor(private foursquareService:FoursquareService, private fb:FormBuilder) {
    this.form = fb.group({
      'keyword': [null, Validators.required]
    });
  }

  oauthToken: string = 'KMDE3Z5WV2YIWHE4JNGW41EVH4D01EZ2QDJYBMAF31BKSGD';

  ngOnInit() {
  }

  searchVenues(form){
    
    this.foursquareService.getLocation().subscribe(res => {
      let location = res.loc;

      this.foursquareService.getVenues(location, form.keyword).subscribe(res => {
        
        this.results = [];

        for(let i = 0; i < res.response.venues.length; i++){
          this.results.push({
            name: res.response.venues[i].name,
            id: res.response.venues[i].id,
            location: {
              street: res.response.venues[i].location.formattedAddress[0],
              cityState: res.response.venues[i].location.formattedAddress[1]
            }
          })
        }
        console.log(this.results)
      })
    })
  }
    /*this.foursquareService.getVenues().subscribe(res => {
      console.log(res)
      console.log(res.response.venues[0].name)
      for(let i = 0; i < res.response.venues.length; i++){
        console.log(res.response.venues[i].name);
      }
    })*/
  

}



