import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  form: FormGroup;

  constructor(
    private fb: FormBuilder, 
    private authService: AuthService,
    private flashMessages:FlashMessagesService,
    private router:Router
    ){

    this.form = fb.group({
      'name': [null, Validators.compose([
        Validators.required,
        Validators.minLength(2),
        Validators.maxLength(30),
        Validators.pattern(/^(([a-zA-Z]{2,30})+[ ]+([a-zA-Z]{2,30})+)+$/)       
      ])],
      'username': [null, Validators.compose([
        Validators.required,
        Validators.minLength(6),
        Validators.maxLength(30),
        Validators.pattern(/^(?!.*?[\W])(?=.*?[a-zA-Z]|=.*?[A-Z]).{3,16}$/)
      ])],
      'email': [null, Validators.compose([
        Validators.required,
        Validators.minLength(2),
        Validators.maxLength(40),
        Validators.pattern(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/)
      ])],
      'password': [null, Validators.compose([
        Validators.required,
        Validators.minLength(8),
        Validators.maxLength(16),
        Validators.pattern(/^(?=.*?[a-zA-Z])(?=.*?[\d])(?=.*?[\W]).{8,16}$/)
      ])]
    })
  }

  ngOnInit() {
  }

  registerUser(user){

      this.authService.registerUser(user).subscribe(data => {
        if(data.success){
          this.flashMessages.show('You have been successfully registered!', {cssClass: 'alert-success', timeout: 3000});
          this.router.navigate(['/login']);
        }else{
          this.flashMessages.show('Oh no, something went wrong!', {cssClass: 'alert-danger', timeout: 3000});
          this.router.navigate(['/register']);
        }
      });
  }

}



