import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { FlashMessagesService } from 'angular2-flash-messages';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {

  contact: boolean = false;
  contactForm: FormGroup;

  constructor(
    private authService:AuthService,
    private router:Router,
    private flashMessage:FlashMessagesService,
    private fb:FormBuilder
    ){
      this.contactForm = fb.group({
        'name': [null, Validators.required],
        'email': [null, Validators.required],
        'message': [null, Validators.required]
      });
    }

  ngOnInit() {
  }

  logout(){
    this.authService.logout();
    this.flashMessage.show('You are now logged out.', {cssClass: 'alert-warning', timeout: 3000});
    this.router.navigate(['/']);
    return false;
  }

  sendMessage(form){
      this.authService.sendMessage(form).subscribe(data => {
        if(data.success){
          this.flashMessage.show('Your message was sent!', {cssClass: 'alert-success', timeout: 3000});
        }else{
          this.flashMessage.show('Oh no, something went wrong!', {cssClass: 'alert-danger', timeout: 3000});
        }
      });
    this.showContact();
  }

  showContact(){
    this.contact === false ? this.contact = true : this.contact = false;
  }

}



