import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { tokenNotExpired } from 'angular2-jwt';
import 'rxjs/add/operator/map';

@Injectable()
export class AuthService {

  authToken: any;
  userPermission: any;
  user: any;

  constructor(private http:Http) { }

  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/register', user, {headers: headers})
      .map(res => res.json());
  }

  login(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/authenticate', user, {headers: headers})
      .map(res => res.json());
  }

  getProfile(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get('users/profile', {headers: headers})
      .map(res => res.json());
  }

  storeUserData(token, user){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

//trying to get permission property from localStorage
  loadPermission(){
    const permission = localStorage.getItem('user');
    this.userPermission = permission[permission];
  }

  loggedIn(){
    return tokenNotExpired('id_token');
  }

//once permission is obtained check what it is
  permission(){
    return 'admin';
  }

  logout(){
    this.authToken = null,
    this.user = null,
    localStorage.clear();
  }

  sendMessage(message){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/message', message, {headers: headers})
      .map(res => res.json());
  }
}



